#footer {
    margin: 0 1rem;
    font-size: .7rem;
    clear: both;
    border-top: 1px solid #000;
    max-width: 22rem;

    #altre-pagine {
        margin-top: 1rem;
        text-transform: lowercase;
        
        a {
            margin-right: .2rem;
        }
    }
    
    #impressum {
        margin-top: 1rem;
    }

    p {
        margin: 0;
    }
}

@media only screen and (max-width: 720px) {
    #footer {
        margin-left: 0;
        margin-right: 0;
    }
}
